import React from 'react';
import connect from '@vkontakte/vkui-connect';
import {View} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import axios from 'axios';

import Home from './panels/Home';
import SimilarMemes from './panels/SimilarMemes';

class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            activePanel: 'home',
            fetchedUser: null,
            similarId: 0,
            memes: [
                {
                    "id": 1,
                    "text": "1",
                    "url": "https://pp.userapi.com/c845122/v845122360/12e67b/Uo-DGS0Ve1I.jpg"
                },
                {
                    "id": 2,
                    "text": "2",
                    "url": "https://pp.userapi.com/c845122/v845122360/12e64a/ZMWm0n16u7I.jpg"
                },
                {
                    "id": 3,
                    "text": "3",
                    "url": "https://pp.userapi.com/c845122/v845122360/12e643/bBtM9xUQfGk.jpg"
                }],
            similarMemes: [
                {
                    "id": 21,
                    "text": "21",
                    "url": "https://pp.userapi.com/c845122/v845122360/12e666/HAN6Jv93bYw.jpg"
                },
                {
                    "id": 22,
                    "text": "22",
                    "url": "https://pp.userapi.com/c845122/v845122360/12e635/Nz9s16ksfOk.jpg"
                },
                {
                    "id": 23,
                    "text": "23",
                    "url": "https://pp.userapi.com/c845122/v845122360/12e62e/GxWDm2bbMio.jpg"
                }]
        };
    }

    componentDidMount() {
        connect.subscribe((e) => {
            switch (e.detail.type) {
                case 'VKWebAppGetUserInfoResult':
                    this.setState({fetchedUser: e.detail.data});
                    // fetchUser.id
                    break;
                default:
                    console.log(e.detail.type);
            }
        });
        connect.send('VKWebAppGetUserInfo', {});
        // this.getMemes();
    }

    getMemes = () => {
        axios.get("https://95.213.28.127:8443/get_memes/1").then(response => {
            this.setState({memes: this.state.memes.concat(response.data)});
            console.log(this.state.memes);
            // var a = this.state.memes;
            // for (var i = 0; i < response.data.length; i++) {
            //     a.push(response.data[i]);
            // }
            // console.log(a);
            // this.setState({memes: a});
        })
    };

    go = (e) => {
        this.setState({activePanel: e.currentTarget.dataset.to});
    };

    openSimilar = (e) => {
        // TODO: get request for similars here!
        this.setState({activePanel: e.currentTarget.dataset.to});
        this.setState({similarId: e.currentTarget.dataset.similar});
    };


    render() {
        return (
            <View  activePanel={this.state.activePanel}>
                <Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} openSimilar={this.openSimilar}
                      memes={this.state.memes} getMemes={this.getMemes}/>
                <SimilarMemes id="similar-memes" go={this.go} similarId={this.state.similarId}
                              openSimilar={this.openSimilar} similarMemes={this.state.similarMemes}/>
            </View>
        );
    }
}

export default App;



